.return:focus, .copy:focus, #shareBtn:focus{
    background-color: gold;
  }
#Infomap{
    width:300px;
    height:300px;
    position:relative;
    left:600px;
}
#map{
    width:1400px;
    height:550px;
    position:relative;
    left:50px;

}
@media (max-width:600px){ /*가로 너비가 600px 이하일 때 아래 스타일 적용*/
    #Infomap{
        width:300px;
        height:300px;
        position:relative;
        left:50px;
    }
    #map{
        width:100%;
        height:350px;
        position:relative;
        left:10px;
    
    }
}